/** Grid **/
/** Button **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.section {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.center-content {
  max-width: 80rem;
}

.right-align {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.yellow-button {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.yellow-button:hover {
  background-color: #c77808;
  cursor: pointer;
}

.yellow-button-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.yellow-button-block:hover {
  background-color: #c77808;
  cursor: pointer;
}

.green-button {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.green-button:hover {
  background-color: #619800;
  cursor: pointer;
}

.green-button-block {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.green-button-block:hover {
  background-color: #619800;
  cursor: pointer;
}

.red-button-block {
  background-color: #af0808;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  width: 100%;
}
.red-button-block:hover {
  background-color: #970707;
  cursor: pointer;
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #e1e1e1;
}
.dark-mode .advertisement,
.dark-mode .property-summary {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark-mode .advertisement .price,
.dark-mode .property-summary .price {
  color: #71B100;
}
.dark-mode .advertisement .icon,
.dark-mode .property-summary .icon {
  filter: invert(100);
}
.dark-mode .about-text blockquote {
  color: #FFFFFF;
}
.dark-mode .form label, .dark-mode .form legend,
.dark-mode .form p {
  color: #e1e1e1;
}
.dark-mode .form input:not([type=submit]),
.dark-mode .form textarea, .dark-mode .form select {
  background-color: #333333;
  border: 1px solid #333333;
  color: #FFFFFF;
}

/** UI **/
.no-webp .start {
  background-image: url(../img/header.jpg);
}

.webp .start {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.start {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.start {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.start .header-content {
    min-height: 70rem;
  }
}

.header-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.header-content h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}

.footer {
  background-color: #333333;
  padding: 2.5rem 0;
  margin-bottom: 0;
}
.footer-content {
  padding-top: 2.5rem;
}
.footer .navigation {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

.right-side {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .right-side {
    align-items: flex-end;
  }
}
.right-side .dark-mode-button {
  max-width: 3rem;
  margin-top: 3rem;
  margin-bottom: 2rem;
  filter: invert(100%);
}
.right-side .dark-mode-button:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .right-side .dark-mode-button {
    margin-top: 0;
  }
}

.bar {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .bar {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}
.bar img {
  width: 70%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .bar img {
    width: 100%;
  }
}

.navigation {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: opacity 0.5s linear;
}
@media (min-width: 768px) {
  .navigation {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navigation a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
.navigation a:hover {
  color: #71B100;
}
@media (min-width: 768px) {
  .navigation a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navigation a:last-of-type {
    margin-right: 0;
  }
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.show {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.icon {
  text-align: center;
}
.icon img {
  height: 10rem;
}
.icon h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .icons-about {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (min-width: 768px) {
  .container-advertisements {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.advertisement {
  background-color: #eeeeee;
  border: 1px solid #c8c8c8;
  margin-bottom: 2rem;
}
.advertisement .advertisement-content {
  padding: 2rem;
}
.advertisement h3, .advertisement p {
  margin: 0;
}

.price {
  color: #71B100;
  font-weight: 700;
  font-size: 3rem;
}

.icons-especifications {
  display: flex;
  list-style: none;
  padding: 0;
  justify-content: space-around;
}
.icons-especifications li {
  display: flex;
}
.icons-especifications li img {
  padding-right: 2rem;
}
.icons-especifications li p {
  font-weight: 700;
}

.icons-align {
  max-width: 35rem;
  justify-content: space-between;
}

.no-webp .contact-image {
  background-image: url(../img/find.jpg);
}

.webp .contact-image {
  background-image: url(../img/find.webp);
}

.contact-image {
  background-position: center center;
  background-size: cover;
  padding: 10rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contact-image h2 {
  font-size: 4rem;
  font-weight: 700;
  color: #FFFFFF;
}
.contact-image p {
  font-size: 1.8rem;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .lower-section {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.blog-entry {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .blog-entry {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.blog-entry:last-of-type {
  margin-bottom: 0;
}

.text-entry a {
  color: #000000;
  margin-top: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .text-entry a {
    margin-top: 0;
  }
}
.text-entry h4 {
  margin: 0;
  line-height: 1.4;
}
.text-entry h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 1rem;
}
.text-entry span {
  color: #E08709;
}

.comment {
  background-color: #71B100;
  font-size: 2.4rem;
  padding: 2rem;
  color: #FFFFFF;
  border-radius: 2rem;
}
.comment blockquote {
  padding-left: 5rem;
  position: relative;
}
.comment blockquote::before {
  content: "";
  display: block;
  position: absolute;
  height: 8rem;
  width: 6rem;
  background-image: url(../img/quote.svg);
  background-repeat: no-repeat;
  left: -2rem;
}
.comment p {
  text-align: right;
  color: #FFFFFF;
}

.form p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.form legend {
  font-size: 2rem;
  color: #333333;
}
.form label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.form input:not([type=submit]),
.form textarea, .form select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.form textarea {
  height: 20rem;
}
.form .form-contact {
  display: flex;
  max-width: 30rem;
  justify-content: space-between;
  align-items: center;
}
.form input[type=radio] {
  margin: 0;
  width: auto;
}

/** Internals **/
@media (min-width: 768px) {
  .about-content {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.about-text blockquote {
  font-weight: 700;
  font-size: 2.2rem;
  margin: 3rem 0 0 0;
  padding-left: 1rem 0 3rem 0;
}

.alert {
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem 0;
}
.alert.error {
  background-color: #af0808;
}
.alert.success {
  background-color: #71B100;
}

table.properties {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.properties thead {
  background-color: #71B100;
}
table.properties thead th {
  color: #FFFFFF;
  padding: 2rem;
}
table.properties tbody td {
  text-align: center;
}
table.properties .table-image {
  width: 10rem;
}

.small-image {
  width: 20rem;
}